
import {motion} from 'framer-motion'
import { InView } from 'react-intersection-observer';


const SlashImageEffect=({images,width,height})=>{

  console.log(width,height)
    return(
        <>


      
        <motion.div   className={`w-[${width}] h-[${height}] `}>
           
        <InView >
      {({ inView, ref }) => (
        
         <div ref={ref} className={`w-[${width}] h-[${height}] relative  overflow-hidden`}>
{inView && (
<>
            <motion.div
               className='bg-black z-[10] absolute top-0 left-0 w-[30px]'
            animate={{
                width:['30px','30px',width,width],
                height:['0px',height,height],
                x:['0px','0px','0px',width],

                transition:{
                    duration:1,
                    times:[0,0.5,0.7,1],
                   
                }


            }}
            
            
            >



            
            </motion.div>

            <motion.img


            initial={{scale:1.2}}
            animate={{
                opacity:[0,1],
                x:['0px','20px'],
                transition:{
                    delay:0.8,
                    duration:0.3,
                    times:[0,1]

                    
                }
            }}
            
            className='w-[100%] relative z-[5] h-[100%] '  src={require('./services2.png')} />



           </>
)}
            </div>


        )}

        </InView >





    
        </motion.div>
        
       
        
        
        
        
        
        
        
        
        
        </>
    )







}






export default SlashImageEffect;





